<template>
    <div>
        <button @click="Componentcombine()">Combine Node</button>
        <button @click="switchlevel()">Switch Level</button>
        <button @click="splitcom()">Split Component</button>
        <button v-if="newflag" @click="backtoComponent()">Back</button>
        <p>{{ message }}</p>
        <div ref="chartContainer" :key="componentKey">
        </div>
    </div>
</template>


<script>
import * as d3 from 'd3';
export default {
    name: 'TopLevel',
    data() {
        return {
            nodesIN: null,
            linksIN: null,
            listall: null,
            simulation: null,
            svgWidth: 1400,
            svgHeight: 800,
            nodeRadius: 16,
            flag: true,
            newflag: false,        //是否显示back按键
            componentKey: 0,
            selectedNodes: [],
            componentNum: 0,
            message: "Component Level",
            timer: null
        };
    },
    async mounted() {
        try {
            // const response1 = await fetch("./assets/FB1.json");
            const response1 = await fetch(`${this.$basePath}assets/FB1.json`);
            if (!response1.ok) throw new Error('Network response was not ok');
            const graphData1 = await response1.json();
            let nodes2 = graphData1.nodes.map(d => ({ ...d }));
            let links2 = graphData1.links.map(d => ({ ...d }));
            // const response2 = await fetch("./assets/festo.json"); // 使用实际的JSON文件路径
            const response2 = await fetch(`${this.$basePath}assets/festo.json`);
            if (!response2.ok) throw new Error('Network response was not ok');
            const graphData2 = await response2.json();
            let nodes1 = graphData2.nodes.map(d => ({ ...d }));
            let links1 = graphData2.links.map(d => ({ ...d }));
            this.listall = [{ "keyid": "ComponentLevel", "nodes": nodes1, "links": links1 }]
            links2 = links2.filter(d => {
                // 根据您的要求进行过滤
                return (d.source !== "FB1" && d.target !== "FB1"); // 这里示例删除 ID 为 2 的节点
            });
            nodes2 = nodes2.filter(d => {
                // 根据您的要求进行过滤
                return d.id !== "FB1"; // 这里示例删除 ID 为 2 的节点
            });
            this.linksIN = links2;
            this.nodesIN = nodes2;
            this.renderChart();
            let midvar = { "keyid": "ButtomLevel", "nodes": this.nodesIN, "links": this.linksIN }
            this.listall.push(midvar)
            this.linksIN = links1;
            this.nodesIN = nodes1;
            this.clearGraph();
            this.renderChart();
            // const response3 = await fetch("./assets/FB.json");
            const response3 = await fetch(`${this.$basePath}assets/FB.json`);
            if (!response3.ok) throw new Error('Network response was not ok');
            const graphData3 = await response3.json();
            let nodes3 = graphData3.nodes.map(d => ({ ...d }));
            let links3 = graphData3.links.map(d => ({ ...d }));
            midvar = { "keyid": "FB1", "nodes": nodes3, "links": links3 }
            this.listall.push(midvar)
        } catch (error) {
            console.error("Failed to fetch graph data: ", error);
        }
    },
    methods: {
        renderChart() {
            if (this.listall) {
                console.log("data ok!")
                // const width = 1200;
                // const height = 600;
                // const color = d3.scaleOrdinal(d3.schemeCategory10);
                const margin = 120;
                // The force simulation mutates links and nodes, so create a copy
                // so that re-evaluating this cell produces the same result.
                var links = this.linksIN;
                var nodes = this.nodesIN;
                let height1 = this.svgHeight - 100;
                let spacing = 60;
                //const maxCnt = Math.max(nodes.filter(node => node.group === 1).length, nodes.filter(node => node.group === 2).length)
                nodes.forEach((d) => {
                    // 把group为1的结点固定在最左侧
                    if (d.group === 1) {
                        d.fx = margin; // 固定 x 坐标在最左侧
                        const group1Nodes = nodes.filter(node => node.group === 1);
                        spacing = Math.ceil(height1 / group1Nodes.length)
                        // const spacing = this.svgHeight / (group1Nodes.length + 1);
                        d.fy = spacing * (group1Nodes.indexOf(d) + 1); // 计算均匀分布的 y 坐标
                        //    leftHeight= spacing *(group1Nodes.length + 1);
                        //     d.fy = spacing * (group1Nodes.indexOf(d) + 1);
                    }
                    // 把group为2的结点固定在最右侧
                    if (d.group === 2) {
                        d.fx = this.svgWidth - margin; // 固定 x 坐标在最右侧
                        const group2Nodes = nodes.filter(node => node.group === 2);
                        spacing = Math.floor(height1 / group2Nodes.length)
                        d.fy = spacing * (group2Nodes.indexOf(d) + 1);  // 计算均匀分布的 y 坐标
                        // rightHeight= spacing *(group2Nodes.length + 1);
                        // d.fy = spacing * (group2Nodes.indexOf(d) + 1);
                    }
                });
                // console.log(nodes)
                let midnode = null;
                let midIndex = 0;
                for (let i = 0; i < this.selectedNodes.length; i++) {
                    midnode = nodes.filter(d => {
                        return d.id === this.selectedNodes[i].id
                    })
                    if (midnode[0].group === 3) {
                        midIndex = nodes.indexOf(midnode[0]);
                        nodes[midIndex].group = 7
                    }
                    else if (midnode[0].group === 5) {
                        midIndex = nodes.indexOf(midnode[0]);
                        nodes[midIndex].group = 8
                    }
                    else if (midnode[0].group === 6) {
                        midIndex = nodes.indexOf(midnode[0]);
                        nodes[midIndex].group = 9
                    }
                }
                // 定义了一个力学模拟，在每个tick步骤的时候运行ticked函数
                this.simulation = d3.forceSimulation(nodes)
                    // 这里用distance属性使连线变长
                    .force("link", d3.forceLink(links).id(d => d.id).distance(50))
                    .force("charge", d3.forceManyBody())
                    .force("collide", d3.forceCollide(this.nodeRadius))
                    // .force("center", d3.forceCenter(this.svgWidth / 2, this.svgHeight / 2))
                    // .force("boundingBox", this.boundingBoxForce())
                    .on("tick", this.ticked);

                const svg = d3.select(this.$refs.chartContainer) //选择文档中的chartContainer元素
                    .append('svg') //添加一个svg元素
                    .attr("width", this.svgWidth)
                    .attr("height", this.svgHeight)
                    .attr("viewBox", [0, 0, this.svgWidth, this.svgHeight])
                    .attr("style", "max-width: 100%; height: auto;");

                svg
                    .append('defs')
                    .append('marker')
                    .attr('id', 'arrow')
                    .attr('viewBox', [0, 0, 10, 10])
                    .attr('refX', 5)
                    .attr('refY', 5)
                    .attr('markerWidth', 5)
                    .attr('markerHeight', 5)
                    .attr('orient', 'auto')
                    .append('path')
                    .attr('d', 'M 0 ,0 L 0,10 L 10, 5')
                    .attr('fill', 'black');

                // 创建一个节点组来包含线段和文本
                const link = svg.append("g")//g表示group，一个容器元素，用于将很多svg元素组合到一起
                    .selectAll('g')
                    .data(links)//绑定links数组
                    .join('g');

                // 在线段组中添加线段
                link.append("line")
                    .attr("stroke", "#999")
                    .attr("stroke-opacity", 0.6)
                // .attr("marker-end", "url(#arrowhead)"); // 应用箭头 marker
                // 在线段组中添加线段
                link.append("line")
                    .attr("stroke", "#999")
                    .attr("stroke-opacity", 0.6)
                    .attr('marker-end', 'url(#arrow)'); // 在 line 的结束处添加箭头
                // 在线段组中添加文本
                //link.append("text")
                //.attr('class', 'text-link')
                // .attr("text-anchor", "middle") // 水平居中
                //.attr("dominant-baseline", "central") // 垂直居中
                //.style('fill', "black") // 设置字体颜色
                //.style('font-size', '10px') // 设置字体大小
                //.style('font-family', 'Times New Roman') // 设置字体
                //.style('font-style', 'italic')
                //.text(d => d.linkName); // 设置文本内容
                // 创建一个节点组来包含圆和文本
                const node = svg.append("g")
                    .attr("class", "nodes") // 给组添加一个类，以便我们可以引用它
                    .selectAll("g")
                    .data(nodes)
                    .join("g") // 为每个节点数据创建一个'g'元素
                    .join("selectedNodes")
                    .on('click', this.handleNodeClick)
                    .on('dblclick', this.handleNodeDoubleClick);

                node.each(function (d) {
                    if (d.group === 1) {
                        d3.select(this).append("circle").attr("fill", "#1f77b4")
                    }
                    else if (d.group === 2) {
                        d3.select(this).append("circle").attr("fill", "green")
                    }
                    else if (d.group === 3) {
                        d3.select(this).append("circle").attr("fill", "white")
                    }
                    else if (d.group === 4) {
                        d3.select(this).append("circle").attr("fill", "#fff2cc")
                    }
                    else if (d.group === 5) {
                        d3.select(this).append("circle").attr("fill", "#FFE4E1")
                    }
                    else if (d.group === 7 || d.group === 8) {
                        d3.select(this).append("circle").attr("fill", "#708090")
                    }
                    // 给属于group6的节点添加矩形
                    else if (d.group === 6) {
                        d3.select(this).append("rect")
                            .attr("width", 50)
                            .attr("height", 30)
                            .attr("x", d.x - 25)
                            .attr("x", d.y - 15)
                            .attr("fill", "orange")
                            //.attr("fill", color)
                            .attr("stroke", "black")
                            .attr("stroke-width", 1.5);
                    }
                    else if (d.group === 9) {
                        d3.select(this).append("rect")
                            .attr("width", 50)
                            .attr("height", 30)
                            .attr("x", d.x - 25)
                            .attr("x", d.y - 15)
                            .attr("fill", "#708090")
                            .attr("stroke", "black")
                            .attr("stroke-width", 1.5);
                    }
                });
                // 对所有圆批量设置格式
                const allCircle = this.$refs.chartContainer.querySelectorAll('circle');
                allCircle.forEach(cc => {
                    cc.setAttribute("stroke", "black");
                    cc.setAttribute("stroke-width", 1.5);
                    cc.setAttribute("r", 16);
                });
                // 在节点组中添加圆形
                //node.append("circle")
                //    .attr("r", this.nodeRadius)
                //    // .attr("fill", d => color(d.group))
                //    .attr("fill", d => this.ColorGeneration(d))
                //    .attr("stroke", "black")
                //    .attr("stroke-width", 1.5)

                // 在节点组中添加文本
                node.append("text")
                    .attr('class', 'text-inner')
                    .attr('x', d => d.x)
                    .attr('y', d => d.y)
                    .attr("text-anchor", "middle") // 水平居中
                    .attr("dominant-baseline", "central") // 垂直居中
                    .style('font-size', '10px') // 设置字体大小
                    .style('fill', d => this.assignTextColor(d))
                    .text(d => d.title); // 将节点数据的id设置为文本内容

                node.each(function (d) {
                    // 给属于group1的节点组中根据条件添加第二个文本，位于圆形的左侧
                    if (d.group === 1) {
                        d3.select(this).append("text")
                            .attr("text-anchor", "end") // 文本靠右侧对齐
                            .attr('class', 'text-left')
                            .attr("dominant-baseline", "central") // 垂直居中
                            .style('font-size', '10px') // 设置字体大小
                            .style('fill', "black") // 设置字体颜色
                            .style('font-family', 'Times New Roman') // 设置字体
                            .style('font-weight', 'bold')
                            .text(d => d.name); // 设置文本内容
                    }
                    // 给属于group2的节点组中根据条件添加第二个文本，位于圆形的右侧
                    if (d.group === 2) {
                        d3.select(this).append("text")
                            .attr("text-anchor", "start") // 文本靠右侧对齐
                            .attr('class', 'text-right')
                            .attr("dominant-baseline", "central") // 垂直居中
                            .style('font-size', '10px') // 设置字体大小
                            .style('fill', "black") // 设置字体颜色
                            .style('font-family', 'Times New Roman') // 设置字体
                            .style('font-weight', 'bold')
                            .text(d => d.name); // 设置文本内容
                    }
                    // // 给属于group2的节点组中根据条件添加第二个文本，位于圆形的右侧
                    // if (d.group === 2) {
                    //     d3.select(this).append("text")
                    //         .attr("text-anchor", "middle") // 文本靠右侧对齐
                    //         .attr('class', 'text-bottom')
                    //         .attr("dominant-baseline", "central") // 垂直居中
                    //         .style('font-size', '10px') // 设置字体大小
                    //         .style('fill', "black") // 设置字体颜色
                    //         .style('font-family', 'Times New Roman') // 设置字体
                    //         .style('font-weight', 'bold')
                    //         .text(d => d.name); // 设置文本内容
                    // }
                });

                // 添加拖拽行为，包含开始、进行和结束时的行为
                node.call(d3.drag()
                    .on("start", this.dragstarted)
                    .on("drag", this.dragged)
                    .on("end", this.dragended));
                for (let i = 0; i < this.selectedNodes.length; i++) {
                    midnode = nodes.filter(d => {
                        return d.id === this.selectedNodes[i].id
                    })
                    if (midnode[0].group === 7) {
                        midIndex = nodes.indexOf(midnode[0]);
                        nodes[midIndex].group = 3
                    }
                    else if (midnode[0].group === 8) {
                        midIndex = nodes.indexOf(midnode[0]);
                        nodes[midIndex].group = 5
                    }
                    else if (midnode[0].group === 9) {
                        midIndex = nodes.indexOf(midnode[0]);
                        nodes[midIndex].group = 6
                    }
                }
            }
        },
        Componentcombine() {
            let links1 = JSON.parse(JSON.stringify(this.listall[0].links));
            let nodes1 = JSON.parse(JSON.stringify(this.listall[0].nodes))
            let links1co = JSON.parse(JSON.stringify(links1))
            for (let i = 0; i < nodes1.length; i++) { //将未被选中的节点添加进去
                if (this.selectedNodes.indexOf(nodes1[i]) === -1) {
                    let Outflag = 0;
                    let Inflag = 0;
                    for (let j = 0; j < links1.length; j++) {
                        if (links1[j].source.id === nodes1[i].id) {
                            for (let k = 0; k < this.selectedNodes.length; k++) {
                                console.log('1')
                                if (this.selectedNodes[k].id === links1[j].target.id) {
                                    Outflag += 1;
                                    break;
                                }
                            }
                        }
                        else if (links1[j].target.id === nodes1[i].id) {
                            for (let k = 0; k < this.selectedNodes.length; k++) {
                                console.log('2')
                                if (this.selectedNodes[k].id === links1[j].source.id) {
                                    Inflag += 1;
                                    break;
                                }
                            }
                        }
                    }
                    if (Inflag > 0 && Outflag > 0) {
                        let comparenode = this.selectedNodes.filter(d => {
                            return d.id === nodes1[i].id
                        })
                        if (comparenode.length === 0) {
                            this.selectedNodes.push(nodes1[i]);
                        }
                    }
                }
            }
            console.log(this.selectedNodes)
            this.selectedNodes = this.selectedNodes.filter(d => {
                return d.group !== 6 && d.group !== 9;
            });
            if (this.selectedNodes.length === 0) {
                return
            }
            for (let i = 0; i < this.selectedNodes.length; i++) {
                if (this.selectedNodes[i].group !== 1 && this.selectedNodes[i].group !== 2) {
                    this.selectedNodes[i].index = i;
                    delete this.selectedNodes[i].fx;
                    delete this.selectedNodes[i].fy;
                }
            }
            //命名，生成新的节点名称
            this.componentNum += 1;
            let Numstr = String(this.componentNum);
            let nameforcom = "Com".concat(Numstr);
            let addele = { "id": nameforcom, "title": "Com", "group": 6 };
            let linksadd = [];
            let nodescompare = nodes1;
            nodescompare.push(addele);
            let linkscompare = links1;
            //比较连线
            for (let i = 0; i < this.selectedNodes.length; i++) {
                for (let j = 0; j < links1.length; j++) {
                    if (this.selectedNodes[i].id === links1[j].target.id) {
                        for (let k = 0; k < nodes1.length; k++) {
                            if (links1[j].source.id === nodes1[k].id) {
                                let pertoadd = { "source": nodes1[k], "target": addele };
                                linkscompare.push(pertoadd);
                                linkscompare.splice(j, 1);
                                j--;
                                break;
                            }
                        }
                        continue;
                    }
                    if (this.selectedNodes[i].id === links1[j].source.id) {
                        for (let k = 0; k < nodes1.length; k++) {
                            if (links1[j].target.id === nodes1[k].id) {
                                let pertoadd = { "source": addele, "target": nodes1[k] };
                                linkscompare.push(pertoadd);
                                linkscompare.splice(j, 1);
                                j--;
                                break;
                            }
                        }
                    }
                }
                for (let j = 0; j < nodescompare.length; j++) {
                    if (this.selectedNodes[i].id === nodescompare[j].id) {
                        nodescompare.splice(j, 1);
                        j--;
                        break;
                    }
                }
            }
            let midnode = null;
            for (let i = 0; i < linkscompare.length; i++) {
                midnode = nodescompare.filter(d => {
                    return d.id === linkscompare[i].source.id
                })
                if (midnode.length > 0.5) {
                    linkscompare[i].source = midnode[0]
                }
                midnode = nodescompare.filter(d => {
                    return d.id === linkscompare[i].target.id
                })
                if (midnode.length > 0.5) {
                    linkscompare[i].target = midnode[0]
                }
            }
            this.listall[0].links = linkscompare;
            this.listall[0].nodes = nodescompare
            let Startnum = 1;
            let Endnum = 1;
            for (let i = 0; i < this.selectedNodes.length; i++) {
                delete this.selectedNodes[i].fx
                delete this.selectedNodes[i].fx
            }
            let Nodessave = JSON.parse(JSON.stringify(this.selectedNodes));
            let addnode = null;
            //创造连线
            for (let i = 0; i < this.selectedNodes.length; i++) {
                for (let j = 0; j < links1co.length; j++) {
                    if (links1co[j].target.id === this.selectedNodes[i].id) {
                        midnode = this.selectedNodes.filter(d => {
                            return links1co[j].source.id === d.id
                        })
                        if (links1co[j].source.group === 1) {
                            let addname = "?B".concat(String(Startnum));
                            addnode = { "id": addname, "title": addname, "name": links1co[j].source.name, "group": 1 }
                            Nodessave.push(addnode)
                            linksadd.push({ "source": addnode, "target": this.selectedNodes[i] })
                            Startnum++;
                        }
                        else if (midnode.length < 0.5) {
                            let addname = "?B".concat(String(Startnum));
                            addnode = { "id": addname, "title": addname, "group": 1 }
                            Nodessave.push(addnode)
                            linksadd.push({ "source": addnode, "target": this.selectedNodes[i] })
                            Startnum++;
                        }
                        else if (midnode.length > 0.5) {
                            linksadd.push({ "source": midnode[0], "target": this.selectedNodes[i] })
                        }
                    }
                    else if (links1co[j].source.id === this.selectedNodes[i].id) {
                        midnode = this.selectedNodes.filter(d => {
                            return links1co[j].target.id === d.id
                        })
                        if (links1co[j].target.group === 1) {
                            let addname = "!B".concat(String(Endnum));
                            addnode = { "id": addname, "title": addname, "name": links1co[j].target.name, "group": 2 }
                            Nodessave.push(addnode)
                            linksadd.push({ "source": this.selectedNodes[i], "target": addnode })
                            Endnum++;
                        }
                        if (midnode.length < 0.5) {
                            let addname = "!B".concat(String(Endnum));
                            addnode = { "id": addname, "title": addname, "group": 2 }
                            Nodessave.push(addnode)
                            linksadd.push({ "source": this.selectedNodes[i], "target": addnode })
                            Endnum++;
                        }
                        else if (midnode.length > 0.5) {
                            linksadd.push({ "source": this.selectedNodes[i], "target": midnode[0] })
                        }
                    }
                }
            }
            linksadd = linksadd.filter(d => {
                return d.source.id !== d.target.id
            })
            for (let i = 0; i < linksadd.length; i++) {
                for (let j = i + 1; j < linksadd.length; j++) {
                    if (linksadd[i].source === linksadd[j].source && linksadd[i].target === linksadd[j].target) {
                        linksadd.splice(j, 1)
                        break;
                    }
                }
                midnode = Nodessave.filter(d => {
                    return d.id === linksadd[i].source.id
                })
                linksadd[i].source = midnode[0]
                midnode = Nodessave.filter(d => {
                    return d.id === linksadd[i].target.id
                })
                linksadd[i].target = midnode[0]
            }
            for (let j = 0; j < linksadd; j++) {
                delete linksadd[j].source.x;
                delete linksadd[j].source.y;
                delete linksadd[j].source.vx;
                delete linksadd[j].source.vy;
                delete linksadd[j].source.fx;
                delete linksadd[j].source.fy;
                delete linksadd[j].target.x;
                delete linksadd[j].target.y;
                delete linksadd[j].target.vx;
                delete linksadd[j].target.vy;
                delete linksadd[j].target.fx;
                delete linksadd[j].target.fy;
            }
            for (let j = 0; j < Nodessave; j++) {
                delete Nodessave[j].x;
                delete Nodessave[j].y;
                delete Nodessave[j].vx;
                delete Nodessave[j].vy;
                delete Nodessave[j].fx;
                delete Nodessave[j].fy;
            }
            this.listall.push({ "keyid": nameforcom, "nodes": Nodessave, "links": linksadd })
            this.selectedNodes = [];
            if (this.flag) {
                this.clearGraph();
                this.linksIN = linkscompare;
                this.nodesIN = nodescompare;
                this.svgWidth = 1400;
                this.svgHeight = 800;
                this.newflag = false;
                this.message = "Component Level";
                this.renderChart();
            }
        },
        handleNodeClick(_, d) {
            clearTimeout(this.timer)
            // 点击事件处理逻辑
            this.timer = setTimeout(() => {
                let rerend = false;
                let isSelected = false;
                for (let i = 0; i < this.selectedNodes.length; i++) {
                    if (this.selectedNodes[i].id === d.id) {
                        isSelected = true;
                        rerend = true;
                        this.selectedNodes.splice(i, 1); // 如果节点已经被选中，从选中列表中移除它
                        break;
                    }
                }
                if (!isSelected && (d.group > 2.5 && d.group !== 4)) {
                    rerend = true;
                    this.selectedNodes.push(d); // 否则，将节点添加到选中列表中
                }
                if (rerend) {
                    this.clearGraph();
                    this.renderChart();
                }
            }, 250);
        },


        handleNodeDoubleClick(_, d) {
            // 双击点击事件处理逻辑
            clearTimeout(this.timer);
            this.selectedNodes = [];
            if (d.group !== 6) {
                return
            }
            for (let i = 0; i < this.listall.length; i++) {
                if (this.listall[i].keyid === d.id) {
                    this.linksIN = this.listall[i].links;
                    this.nodesIN = this.listall[i].nodes;
                    this.svgHeight = 800;
                    this.svgWidth = 1400;
                    this.newflag = true;
                    this.message = "Component Level -> ".concat(this.listall[i].keyid);
                    this.clearGraph();
                    this.renderChart();
                    return
                }
            }
        },


        splitcom() {
            let willbesplited = this.selectedNodes.filter(d => {
                return d.group === 6;
            });
            if (willbesplited.length === 0) {
                return
            }
            let links1 = JSON.parse(JSON.stringify(this.listall[0].links));
            let links2 = JSON.parse(JSON.stringify(this.listall[1].links));
            let nodes1 = JSON.parse(JSON.stringify(this.listall[0].nodes));
            let cycflag = false;
            let midlink = null;
            let midnode = null;
            for (let i = 0; i < willbesplited.length; i++) {
                for (let k = 0; k < this.listall.length; k++) {
                    if (this.listall[k].keyid === willbesplited[i].id) {
                        cycflag = true;
                        let nodesnow = JSON.parse(JSON.stringify(this.listall[k].nodes));
                        nodesnow = nodesnow.filter(d => {
                            return d.group !== 1 && d.group !== 2
                        });
                        //删除与component相关的连线
                        for (let j = 0; j < links1.length; j++) {
                            if (links1[j].source.id === willbesplited[i].id) {
                                links1.splice(j, 1);
                                j--;
                                continue
                            }
                            else if (links1[j].target.id === willbesplited[i].id) {
                                links1.splice(j, 1);
                                j--;
                                continue
                            }
                            midnode = nodes1.filter(d => {
                                return d.id === links1[j].source.id
                            })
                            links1[j].source = midnode[0];
                            midnode = nodes1.filter(d => {
                                return d.id === links1[j].target.id
                            })
                            links1[j].target = midnode[0];
                        }
                        //找到与内部相关的连线，添加回links1
                        for (let j = 0; j < links2.length; j++) {
                            for (let nodenum = 0; nodenum < nodesnow.length; nodenum++) {
                                if (nodesnow[nodenum].id === links2[j].source.id) {
                                    midlink = links2[j];
                                    midlink.source = nodesnow[nodenum];
                                    midlink.source.index = midlink.source.index - 1
                                    midnode = nodesnow.filter(d => {
                                        return d.id === links2[j].target.id
                                    })
                                    if (midnode.length > 0) {
                                        midlink.target = midnode[0];
                                        midlink.target.index = midlink.target.index - 1
                                    }
                                    else {
                                        midnode = nodes1.filter(d => {
                                            return d.id === links2[j].target.id
                                        });
                                        if (midnode.length > 0.5) {
                                            midlink.target = midnode[0];
                                        }
                                        else {
                                            for (let newi = 2; newi < this.listall.length; newi++) {
                                                if (newi === k) {
                                                    continue
                                                }
                                                for (let newj = 0; newj < this.listall[newi].nodes.length; newj++) {
                                                    if (this.listall[newi].nodes[newj].id === links2[j].target.id) {
                                                        midnode = nodes1.filter(d => {
                                                            return d.id === this.listall[newi].keyid
                                                        })

                                                        midlink.target = midnode[0]
                                                        break
                                                    }
                                                }
                                            }
                                        }
                                    }
                                    links1.push(midlink);
                                    links2.splice(j, 1);
                                    j--
                                    break
                                }
                                else if (nodesnow[nodenum].id === links2[j].target.id) {
                                    midlink = links2[j];
                                    midlink.target = nodesnow[nodenum];
                                    midlink.target.index = midlink.target.index - 1
                                    midnode = nodesnow.filter(d => {
                                        return d.id === links2[j].source.id
                                    })
                                    //另一个节点在Component内部
                                    if (midnode.length > 0) {
                                        midlink.source = midnode[0];
                                        midlink.source.index = midlink.source.index - 1
                                    }
                                    //不在
                                    else {
                                        midnode = nodes1.filter(d => {
                                            return d.id === links2[j].source.id
                                        });
                                        if (midnode.length > 0) {
                                            midlink.source = midnode[0];
                                        }
                                        else {
                                            for (let newi = 2; newi < this.listall.length; newi++) {
                                                if (newi === k) {
                                                    continue
                                                }
                                                for (let newj = 0; newj < this.listall[newi].nodes.length; newj++) {
                                                    if (this.listall[newi].nodes[newj].id === links2[j].source.id) {
                                                        midnode = nodes1.filter(d => {
                                                            return d.id === this.listall[newi].keyid
                                                        })
                                                        midlink.source = midnode[0]

                                                        break
                                                    }
                                                }
                                            }
                                        }
                                    }
                                    links1.push(midlink);
                                    console.log(midlink)
                                    links2.splice(j, 1);
                                    j--;
                                    break
                                }
                            }
                        }
                        for (let j = 0; j < nodes1.length; j++) {
                            if (nodes1[j].id === willbesplited[i].id) {
                                nodes1.splice(j, 1)
                                break;
                            }
                        }
                        nodes1 = nodes1.concat(nodesnow)
                    }
                    if (cycflag) {
                        cycflag = false;
                        this.listall.splice(k, 1)
                        break
                    }
                }
            }
            this.selectedNodes = [];
            this.listall[0].links = links1;
            this.listall[0].nodes = nodes1;
            this.clearGraph();
            this.linksIN = links1;
            this.nodesIN = nodes1;
            this.flag = true;
            this.newflag = false;
            this.svgWidth = 1400;
            this.svgHeight = 800;
            this.message = "Component Level";
            this.renderChart();
        },



        clearGraph() {
            d3.select(this.$refs.chartContainer).selectAll('*').remove(); // 清除之前生成的图像元素
        },

        switchlevel() {
            this.clearGraph();
            if (this.flag) {
                this.linksIN = this.listall[1].links;
                this.nodesIN = this.listall[1].nodes;
                this.flag = false;
                this.newflag = false;
                this.svgHeight = 1000;
                this.svgWidth = 2400;
                this.message = "Bottom Level";
                this.renderChart();
            }
            else {
                this.linksIN = this.listall[0].links;
                this.nodesIN = this.listall[0].nodes;
                this.flag = true;
                this.newflag = false;
                this.svgWidth = 1400;
                this.svgHeight = 800;
                this.message = "Component Level";
                console.log(this.listall)
                this.renderChart();
            }
        },
        backtoComponent() {
            this.clearGraph();
            this.linksIN = this.listall[0].links;
            this.nodesIN = this.listall[0].nodes;
            this.flag = true;
            this.svgWidth = 1400;
            this.svgHeight = 800;
            this.message = "Component Level";
            this.newflag = false;
            this.renderChart();
        },
        ticked() {
            const link = this.$refs.chartContainer.querySelectorAll('line');
            const circle = this.$refs.chartContainer.querySelectorAll('circle');
            const rects = this.$refs.chartContainer.querySelectorAll('rect');
            const textInner = this.$refs.chartContainer.querySelectorAll('.text-inner');
            const textLeft = this.$refs.chartContainer.querySelectorAll('.text-left');
            const textRight = this.$refs.chartContainer.querySelectorAll('.text-right');
            const textLink = this.$refs.chartContainer.querySelectorAll('.text-link');
            const textBottom = this.$refs.chartContainer.querySelectorAll('.text-bottom');

            link.forEach(lk => {
                // 使连线的结束位置在圆形结点边界上而非圆心
                const x1 = lk.__data__.source.x;
                const y1 = lk.__data__.source.y;
                const x2 = lk.__data__.target.x;
                const y2 = lk.__data__.target.y;
                const theta = Math.atan2((y2 - y1), (x2 - x1));
                lk.setAttribute('x1', x1);
                lk.setAttribute('y1', y1);
                lk.setAttribute('x2', x2 - 18 * Math.cos(theta));
                lk.setAttribute('y2', y2 - 18 * Math.sin(theta));
                // lk.setAttribute('x1', lk.__data__.source.x);
                // lk.setAttribute('y1', lk.__data__.source.y);
                // lk.setAttribute('x2', lk.__data__.target.x);
                // lk.setAttribute('y2', lk.__data__.target.y);
            });

            circle.forEach(nd => {
                nd.setAttribute('cx', nd.__data__.x);
                nd.setAttribute('cy', nd.__data__.y);
            });

            rects.forEach(td => {
                td.setAttribute('x', td.__data__.x - 25);
                td.setAttribute('y', td.__data__.y - 15);
            });

            textInner.forEach(td => {
                td.setAttribute('x', td.__data__.x);
                td.setAttribute('y', td.__data__.y);
            });

            textLeft.forEach(tld => {
                tld.setAttribute('x', tld.__data__.x - this.nodeRadius - 20);
                tld.setAttribute('y', tld.__data__.y);
            });
            textRight.forEach(trd => {
                trd.setAttribute('x', trd.__data__.x + this.nodeRadius + 20);
                trd.setAttribute('y', trd.__data__.y);
            });
            textBottom.forEach(trd => {
                trd.setAttribute('x', trd.__data__.x);
                trd.setAttribute('y', trd.__data__.y + this.nodeRadius + 10);
            });
            textLink.forEach(tl => {
                tl.setAttribute('x', 0.5 * (tl.__data__.source.x + tl.__data__.target.x));
                tl.setAttribute('y', 0.5 * (tl.__data__.source.y + tl.__data__.target.y));
            });

        },



        dragstarted(event) {
            if (!event.active) this.simulation.alphaTarget(0.3).restart();
            event.subject.fx = event.subject.x;
            event.subject.fy = event.subject.y;
        },

        dragged(event, d) {
            if (d.group === 1 || d.group === 2) {
                event.subject.fy = event.y; // 只更新 y，保持 x 固定
            } else {
                event.subject.fx = event.x;
                event.subject.fy = event.y;
            }

        },

        dragended(event, d) {
            if (!event.active) this.simulation.alphaTarget(0);
            if (d.group !== 1 && d.group !== 2) {
                event.subject.fx = null;
            }
            event.subject.fy = null;
        },

        assignTextColor(d) {
            if (d.group === 1 || d.group === 2) {
                return "white"
            }
            else {
                return "black"
            }
        }
    },

    beforeUnmount() {
        // 停止模拟
        if (this.simulation) {
            this.simulation.stop();
        }
    },

}
</script>

<style>
/* 全局样式 */
.button-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    position: fixed;
    /* 或者使用 fixed 根据需求 */
    top: 20;
    left: 0;
    right: 0;
}
</style>
